import {previewPicture} from "../js/post_function.js"
export default{
	name:'review_photo',
	props:{
		images:Array,
	},
	methods:{
		deleteImage(i){
			this.$emit("childData",i)
		},
		reviewImage(i){
			previewPicture(i,this.images)
			.then((res)=>{
				console.log(res)
			})
			.catch((err)=>{
				console.log(err)
			})
		}
		
	}
}
